<!--  -->
<template>
    <div class="content-body">
      <el-form ref="formList" :model="formList" label-width="110px">
        <el-row :gutter="10">
            <el-col>
                <el-form-item label="" prop="accountTime" :rules="timeRules">
                  <el-date-picker  type="datetimerange" @change="changeDateTime" range-separator="" start-placeholder=""  end-placeholder="" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" v-model="formList.accountTime">
                  </el-date-picker>
                </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
              <el-col :span="12">
                <el-form-item label="" prop="consumptionNum" :rules="consumptionNumRules">
                  <el-input v-model="formList.consumptionNum" @change="changeprice"></el-input>
                </el-form-item>
              </el-col>
          </el-row>
          <el-row :gutter="10" v-for="(item,index) in formList.priceRangeList"   :key="item.guid" class="el-price">
                <el-col :span="12">
                  <el-form-item label="" :prop="'priceRangeList.' +index +'.logical_relation'" :rules="logical_relationRules">
                      <el-select  clearable v-model="item.logical_relation" @change="changeLogicType">
                          <el-option value=">" label=""></el-option>
                          <el-option value="<" label=""></el-option>
                          <el-option value="=" label=""></el-option>
                          <el-option value=">=" label=""></el-option>
                          <el-option value="<=" label=""></el-option>
                      </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item :prop="'priceRangeList.' +index +'.price'" :rules="priceNumRules">
                        <el-input type="number"  @change="changeprice" v-model="item.price" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
          </el-row>
          <div class="div_center">
            <div>
              <span class="tip_red" v-if="!isClickSaveData"></span>
              <span class="tip_green" v-if="isClickSaveData"></span>
            </div>
            <el-button type="primary" plain @click="saveConditionData" class="saveData"></el-button>
          </div>
        </el-form>
    </div>
</template>
<script>
/* eslint-disable */
  export default
  {
    // 
    props:{
      conditionKey:{type:String},conditionData:{type:Object,default:function(){
        return {}
      }},
      renderFlag:{type:Boolean,default:false},
      renderData:null,logicRelationFlag:{type:Boolean,default:false},
    },
    data()
    {
      var checkTimesNum = (rule,value,callback) => {
        setTimeout(() => {
          var myreg=/^[0-9]*[1-9][0-9]*$/;
          if (!myreg.test(value)&&value!="") {
            callback(new Error('0'));
          } else {
            callback();
          }
        }, 200);
      };
      var checkPriceNum = (rule,value,callback) => {
        setTimeout(() => {
           var myreg=/^[0-9]*[1-9][0-9]*$/;
          if (value!=""&&value<=0) {
            callback(new Error('0'));
          }else {
            if(value.split('.').length>1){
                if (value.split('.')[1].length > 2) {
                    callback(new Error(''));
                }else if(value==""){
                  new Error('')
                }else{
                  callback();
                }
            }else{
              callback();
            }
          }


        }, 200);
      };
      return {
        formList:{
          consumptionNum:"",
          accountTime:[],//
          priceRangeList:[ {guid:this.guid(),price:'',logical_relation:''}],//
        },
        accountTimePickerOption: {//
          disabledDate(time){
            return time.getTime() < (Date.now()-3600*1000*24);
          }
        },
        consumptionNumRules: {
          required: false, validator: checkTimesNum, trigger: 'change'
        },
        timeRules: {
            required: true, message: '', trigger: 'change'
        },
        logical_relationRules: {
          required: true, message: '', trigger: 'change'
        },
        priceNumRules: [
            {required: true,  trigger: 'blur',message: ''},
            {validator: checkPriceNum}
        ],
        myConditionData:this.conditionData,//-------
        isClickSaveData:false,//
        myRenderFlag:this.renderFlag,
        myRenderData:this.renderData,
        myLogicRelationFlag:this.logicRelationFlag,
      }
    },
    mounted()
    {
      this.init();
    },
     methods: {
      init(){
         if(this.myRenderFlag) this.render();//
      },
      render(){
        this.formList.accountTime=[];//
        this.formList.priceRangeList=[];//
        var data = this.myRenderData;
        this.formList.accountTime=[data.start_time,data.end_time];
        this.formList.consumptionNum=data.recent_pay_num;//
        //
        for(let i=0;i<data.list.length;i++){
          this.formList.priceRangeList.push({price:data.list[i].price,logical_relation:data.list[i].logical_relation,guid:this.guid()});
        }
      },
      //
      changeLogicType(){
        this.changeSaveStatus(false);
      },
      //
      changeprice(){
        this.changeSaveStatus(false);
      },
      //
      changeDateTime(){
        this.changeSaveStatus(false);
      },
      //
      changeSaveStatus(flag)
      {
        this.isClickSaveData=flag;//
        this.$emit('sendissave',this.isClickSaveData);
      },
      //
      saveConditionData(){
        console.log("d",this.formList.priceRangeList)
        this.$refs["formList"].validate((valid) => {
          if(valid){
            this.myConditionData.logic_condition={
              "condition":{
                "start_time":this.formList.accountTime[0],//
                "end_time":this.formList.accountTime[1],//
                "recent_pay_num": this.formList.consumptionNum,//
                "list": this.formList.priceRangeList//
              }
            }
            console.log(this.myConditionData.logic_condition);
            //
            if(this.myLogicRelationFlag){
              this.changeSaveStatus(true);
            }else{
              this.$emit('validForm');//
            }
          }else{
            return false;
          }
        });
      },
      //guid ,
      guid()
      {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
            return v.toString(16);
        });
      }
    },
    watch:{
      logicRelationFlag(val){
        // console.log("val..",val)
        this.myLogicRelationFlag=val;
        if(!this.myLogicRelationFlag){
          this.changeSaveStatus(false);
        }
      }
    }
  }
</script>
<style>
  .tip_red{
    color:red;
  }
  .tip_green{
    color:green;
  }
  .saveData{
    margin-top:20px;
  }
</style>



