<!--  -->
<template>
    <div class="content-body">
      <el-form ref="formName" :model="formName" label-width="100px" :rules="rules">
        <el-row :gutter="10">
              <el-form-item label="" prop="accountTime">
                <el-date-picker  type="datetimerange" range-separator="" start-placeholder=""  end-placeholder="" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" v-model="formName.accountTime"  @change="changeSaveData">
                </el-date-picker>
             </el-form-item>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="payType">
              <el-select  placeholder="" clearable v-model="formName.payType" @change="changeSaveData">
                  <el-option value="2" label=""></el-option>
                  <el-option value="1" label=""></el-option>
                  <el-option value="0" label=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
             <el-form-item label="" style="float:left;" prop="logic">
              <el-select  placeholder="" clearable v-model="formName.logic" @change="changeSaveData">
                  <el-option value="geq" label=""></el-option>
                  <el-option value="leq" label=""></el-option>
                  <el-option value="eq" label=""></el-option>
              </el-select>
              </el-form-item>
              <el-form-item  prop="orderNum" style="float:left">
                <el-input style="width:150px;" type="number"  v-model="formName.orderNum" @input="changeSaveData">
                </el-input>
              </el-form-item>
          </el-col>
        </el-row>
        <div class="div_center">
              <div>
                  <span class="tip_red" v-if="!isClickSaveData"></span>
                  <span class="tip_green" v-if="isClickSaveData"></span>
              </div>
              <el-button type="primary" plain @click="saveConditionData" class="saveData"></el-button>
        </div>
      </el-form>
    </div>
</template>
<script>
/* eslint-disable */
  export default
  {
    // 
    props:{
      conditionKey:{type:String},conditionData:{type:Object,default:function(){
        return {}
      }},
      renderFlag:{type:Boolean,default:false},
      renderData:null,logicRelationFlag:{type:Boolean,default:false},
    },
    data()
    {
      var checkTimesNum = (rule,value,callback) => {
        setTimeout(() => {
          var myreg=/^[0-9]*[1-9][0-9]*$/;
          if (!myreg.test(value)) {
            callback(new Error(','));
          } else {
            callback();
          }
        }, 200);
      };
      return {
        conditionTypeList:rootData.conditionTypeList,//
        formName: {
          accountTime:[],//
          payType:"2",//
          logic:"geq",//
          orderNum:1,//
        },
        conditionTemp:"",//
        myConditionData:this.conditionData,//-------
        rules: {
            orderNum: [
              {required:true,validator: checkTimesNum, trigger: 'blur' }//
            ],
            accountTime: [
              { required: true, message: '', trigger: 'change' }//
            ],
            logic: [
              { required: true, message: '', trigger: 'change' }//
            ],
            payType: [
              { required: true, message: '', trigger: 'change' }//
            ]

        },
        accountTimePickerOption: {//
          disabledDate(time){
            return time.getTime() < (Date.now()-3600*1000*24);
          }
        },
        isClickSaveData:false,//
        myRenderFlag:this.renderFlag,
        myRenderData:this.renderData,
        myLogicRelationFlag:this.logicRelationFlag,
      }
    },
    mounted()
    {
      this.init();
    },
    methods: {
      init(){
        for(var i in rootData.addConditionTypeTemp){
          if(this.conditionKey==i){
            this.conditionTemp=rootData.addConditionTypeTemp[i];
          }
        }
        //
        if(this.myRenderFlag) this.render();
      },
      render()
      {
        var data = this.myRenderData;
        this.formName.accountTime = [data.pay_start_time,data.pay_end_time];
        this.formName.payType=data.is_pay;
        this.formName.logic=data.order_num_logic;
        this.formName.orderNum=data.order_num;
      },
      //
      changeSaveStatus(flag)
      {
        this.isClickSaveData=flag;//
        this.$emit('sendissave',this.isClickSaveData);
      },
      //
      changeSaveData(){
         this.changeSaveStatus(false);
      },
      saveConditionData(){//
         this.$refs["formName"].validate((valid) => {
          if (valid) {
              this.myConditionData.logic_condition={
                  "condition":{
                    "pay_start_time":this.formName.accountTime[0],//
                    "pay_end_time":this.formName.accountTime[1],//
                    "is_pay": this.formName.payType,
                    "order_num":this.formName.orderNum,
                    "order_num_logic":this.formName.logic
                  }
                }
                //
                if(this.myLogicRelationFlag){
                  this.changeSaveStatus(true);
                }else{
                  this.$emit('validForm');//
                }
          } else {
            return false;
          }
        });
      }
    },
    watch:{
      logicRelationFlag(val){
        // console.log("val..",val)
        this.myLogicRelationFlag=val;
        if(!this.myLogicRelationFlag){
          this.changeSaveStatus(false);
        }
      }
    }
  }
</script>
<style>
  .tip_red{
    color:red;
  }
  .tip_green{
    color:green;
  }
  .saveData{
    margin-top:20px;
  }
</style>



