<!--  -->
<template>
    <div class="content-body">
      <el-form ref="formList" :model="formList" label-width="100px">
        <el-row :gutter="10">
           <el-form-item label="" prop="accountTime" :rules="timeRules">
              <el-date-picker  type="daterange" @change="changeDateTime" range-separator="" start-placeholder=""  end-placeholder="" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" v-model="formList.accountTime">
              </el-date-picker>
            </el-form-item>
        </el-row>
        <el-row :gutter="10" v-for="(item,index) in formList.consumptionList" :key="item.guid">
          <el-col :span="12">
          <el-form-item label="" :prop="'consumptionList.' +index +'.logical_relation'" :rules="logical_relationRules">
              <el-select placeholder="" clearable v-model="item.logical_relation" @change="changeLogicType">
                <el-option value=">" label=""></el-option>
                <el-option value="<" label=""></el-option>
                <el-option value="=" label=""></el-option>
                <el-option value=">=" label=""></el-option>
                <el-option value="<=" label=""></el-option>
              </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item :prop="'consumptionList.' +index +'.after_purchase_num'" :rules="after_purchase_numRules">
              <el-input v-model="item.after_purchase_num" placeholder="" @change="changeafter_purchase_num"></el-input>
          </el-form-item>
        </el-col>
        </el-row>
        <div class="div_center">
            <div>
              <span class="tip_red" v-if="!isClickSaveData"></span>
              <span class="tip_green" v-if="isClickSaveData"></span>
            </div>
            <el-button type="primary" plain @click="saveConditionData" class="saveData"></el-button>
        </div>
      </el-form>
    </div>
</template>
<script>
/* eslint-disable */
  export default
  {
    props:{
      conditionData:{type:Object,default:function(){
        return {}
      }},
      renderFlag:{type:Boolean,default:false},
      renderData:null,logicRelationFlag:{type:Boolean,default:false},
    },
    data()
    {
      var checkTimesNum = (rule,value,callback) => {
        setTimeout(() => {
          var myreg=/^[0-9]*[1-9][0-9]*$/;
          if (!myreg.test(value)) {
            callback(new Error(''));
          } else {
            callback();
          }
        }, 200);
      };
      return {
        formList:{
          consumptionList:[ {guid:this.guid(),after_purchase_num:'',logical_relation:''}],//
          accountTime:"",//
        },
        accountTimePickerOption: {//
          disabledDate(time){
            return time.getTime() < (Date.now()-3600*1000*24);
          }
        },
        myConditionData:this.conditionData,//-------
        after_purchase_numRules: {
          required: true, validator: checkTimesNum, trigger: 'blur'
        },
        logical_relationRules: {
          required: true, message: '', trigger: 'change'
        },
        timeRules: {
            required: true, message: '', trigger: 'change'
        },
        getLogicText:[],//
        isClickSaveData:false,//
        myRenderFlag:this.renderFlag,
        myRenderData:this.renderData,
        myLogicRelationFlag:this.logicRelationFlag,
      }
    },
    mounted()
      {
        this.init();
      },
    methods: {
      init(){
        window.parent.onscroll = (e)=>{
          this.scrollMsg();
        }
        if(this.myRenderFlag) this.render();//
      },
      render(){
        this.formList.accountTime=[];
        this.formList.consumptionList=[];//
        var data = this.myRenderData;
        this.formList.accountTime=[data.start_time,data.end_time]
        //
        for(let i=0;i<data.list.length;i++){
          this.formList.consumptionList.push({after_purchase_num:data.list[i].after_purchase_num,logical_relation:data.list[i].logical_relation,guid:this.guid()});
        }
      },
      //
      changeLogicType(){
        this.changeSaveStatus(false);
      },
      //
      changeafter_purchase_num(){
        this.changeSaveStatus(false);
      },
      //
      changeDateTime(){
        this.changeSaveStatus(false);
      },
      //
      changeSaveStatus(flag)
      {
        this.isClickSaveData=flag;//
        this.$emit('sendissave',this.isClickSaveData);
      },
      //guid ,
      guid()
      {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
            return v.toString(16);
        });
      },
      //
      saveConditionData(){
        this.$refs["formList"].validate((valid) => {
          if(valid){
            this.myConditionData.logic_condition={
              "condition":{
                "start_time":this.formList.accountTime[0],//
                "end_time":this.formList.accountTime[1],//
                "list": this.formList.consumptionList//
              }
            }
            console.log(this.myConditionData.logic_condition);
              //
            if(this.myLogicRelationFlag){
              this.changeSaveStatus(true);
            }else{
              this.$emit('validForm');//
            }
          }else{
            this.alertMsg("");
          }
        });
      },
      alertMsg(msg,type)
      {
        // alert(msg);
        let msgType = type?type:"error";
        this.$message({
          message: msg,
          duration:3600,
          type: msgType
        });
        this.scrollMsg();
      },
      scrollMsg()
      {
        if(self!=top){
          let scrollTop = window.parent.document.documentElement.scrollTop||window.parent.document.body.scrollTop-50;
          let mycss=document.getElementsByClassName("el-message")[0];
          if(mycss){
            mycss.style.cssText="top: "+scrollTop+"px;z-index:1000;";
          }
        }
      }
    },
    watch:{
      logicRelationFlag(val){
        // console.log("val..",val)
        this.myLogicRelationFlag=val;
        if(!this.myLogicRelationFlag){
          this.changeSaveStatus(false);
        }
      }
    }
  }
</script>
<style>
  .tip_red{
    color:red;
  }
  .tip_green{
    color:green;
  }
  .saveData{
    margin-top:20px;
  }
</style>


